export const prettyCountyWord = (counter, oneWord, noWord, aLotOfWord) => {
  if (counter % 10 === 1 && counter !== 11) {
    return oneWord;
  } else if (
    [2, 3, 4].some(el => el === counter % 10) &&
    ![12, 13, 14].some(el => el === counter)
  ) {
    return noWord;
  } else {
    return aLotOfWord;
  }
};

const parseTime = (seconds, timeLimit) => {
  const parser = moment.duration(seconds);
  const weeks = parser.asWeeks();
  const days = parser.asDays();
  const hours = parser.asHours();
  const minutes = parser.asMinutes();
  const limiter = moment(timeLimit, 'HH:mm');
  console.log(seconds < 0);
  console.log(seconds / 1000);
  console.log(
    seconds / 1000 > limiter.format('HH') * 3600 + limiter.format('mm') * 60,
  );
  console.log(limiter.format('HH') * 3600 + limiter.format('mm') * 60);
  if (
    seconds < 0 &&
    -seconds / 1000 > limiter.format('HH') * 3600 + limiter.format('mm') * 60
  ) {
    return 'Ð—Ð°Ð²ÐµÑ€ÑˆÐµÐ½';
  }
  if (seconds / 1000 < 0) return 'Ð¡ÐºÐ¾Ñ€Ð¾ Ð·Ð°Ð²ÐµÑ€ÑˆÐ¸Ñ‚ÑÑ';
  if (days > 7) {
    const date = Math.round(weeks);
    return date + ' ' + prettyCountyWord(date, 'Ð½ÐµÐ´ÐµÐ»Ñ', 'Ð½ÐµÐ´ÐµÐ»Ð¸', 'Ð½ÐµÐ´ÐµÐ»ÑŒ');
  } else if (days > 1) {
    return (
      Math.floor(days) +
      ' ' +
      prettyCountyWord(Math.floor(days), 'Ð´ÐµÐ½ÑŒ', 'Ð´Ð½Ñ', 'Ð´Ð½ÐµÐ¹') +
      ' ' +
      Math.floor(hours - Math.floor(days) * 24) +
      ' ' +
      prettyCountyWord(
        Math.floor(hours - Math.floor(days) * 24),
        'Ñ‡Ð°Ñ',
        'Ñ‡Ð°ÑÐ°',
        'Ñ‡Ð°ÑÐ¾Ð²',
      )
    );
  } else {
    return (
      Math.floor(hours) +
      ' ' +
      prettyCountyWord(Math.floor(hours), 'Ñ‡Ð°Ñ', 'Ñ‡Ð°ÑÐ°', 'Ñ‡Ð°ÑÐ¾Ð²') +
      ' ' +
      Math.floor(minutes - Math.floor(hours) * 60) +
      ' ' +
      prettyCountyWord(
        Math.floor(minutes - Math.floor(hours) * 60),
        'Ð¼Ð¸Ð½ÑƒÑ‚Ð°',
        'Ð¼Ð¸Ð½ÑƒÑ‚Ñ‹',
        'Ð¼Ð¸Ð½ÑƒÑ‚',
      )
    );
  }
};

