import React, { useEffect, useState } from 'react';
import { message, Modal } from 'antd';
import DeleteTest from './DeleteTest';
import firebase from "firebase/app";
import "firebase/firestore";
import { ExclamationCircleOutlined, ExclamationOutlined } from '@ant-design/icons';

const DeleteTestContainer = props => {
  const db = firebase.firestore();
  const [tests, setTests] = useState([]);
  const [fetched, setFetched] = useState(false);
  const [selectedTest, setSelectedTest] = useState();

  const getTests = async () => {
    setFetched(false);
    setTests();
    try {
      await db.collection("tests").get().then((querySnapshot) => {
        const parsed = [];
        querySnapshot.forEach((doc) => {
            parsed.push({
              uid: doc.id,
              name: doc.data().name,
            });
        });
        setTests(parsed);
      });
      setFetched(true)
    } catch (e) {

    }
  }

  const deleteTestModal = async (selectedTest) => {
    Modal.confirm({
      title: 'Ð’Ð½Ð¸Ð¼Ð°Ð½Ð¸Ðµ',
      icon: <ExclamationCircleOutlined />,
      content: 'Ð£Ð´Ð°Ð»Ð¸Ð² Ñ‚ÐµÑÑ‚ Ð²Ñ‹ Ñ‚Ð°Ðº Ð¶Ðµ ÑƒÐ´Ð°Ð»Ð¸Ñ‚Ðµ Ð²ÑÐµ ÑÐµÑÑÐ¸Ð¸ ÑÐ²ÑÐ·Ð°Ð½Ð½Ñ‹Ðµ Ñ ÑÑ‚Ð¸Ð¼ Ñ‚ÐµÑÑ‚Ð¾Ð¼, Ð²Ñ‹ Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ñ…Ð¾Ñ‚Ð¸Ñ‚Ðµ Ð¿Ñ€Ð¾Ð´Ð¾Ð»Ð¶Ð¸Ñ‚ÑŒ?',
      okText: 'Ð”Ð°',
      cancelText: 'ÐÐµÑ‚',
      onOk() {
        deleteTest(selectedTest);
      },
    });
  }

  const deleteTest = async () => {
    try {
      message.loading({ content: 'Ð˜Ð´ÐµÑ‚ ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ðµ', key : 'delete-test'});
      await db.collection("tests").doc(selectedTest).delete();
      const sessions = await db.collection("sessions").where('testId', '==', selectedTest).get();
      for (const ses of sessions.docs) {
        await ses.ref.delete();
      }
      message.success({ content: 'Ð£ÑÐ¿ÐµÑˆÐ½Ð¾', key : 'delete-test'});
      getTests();
      setSelectedTest();
    } catch (e) {
      message.error({ content: 'Ð§Ñ‚Ð¾-Ñ‚Ð¾ Ð¿Ð¾ÑˆÐ»Ð¾ Ð½Ðµ Ñ‚Ð°Ðº', key : 'delete-test'});
      console.log(e);
    }
  }

  useEffect(() => {
    getTests();
  }, [])

  return (
    <DeleteTest deleteTest={deleteTestModal} selectedTest={selectedTest} setSelectedTest={setSelectedTest} fetched={fetched} tests={tests} {...props} />
  );
};

export default DeleteTestContainer;