import React, { useState, useEffect } from 'react';
import ActiveSessions from './Sessions';
import firebase from "firebase/app";
import { message, Modal } from 'antd';
import "firebase/auth";
import "firebase/firestore";
import sendEmail from '../../../utils/sendEmail';
import ReactDOMServer from 'react-dom/server';
import EmaiQuestions from '../../../components/EmaiQuestions/EmaiQuestions';
import { ExclamationCircleOutlined } from '@ant-design/icons';

const ActiveSessionsContainer = props => {
  const [fetched, setFetched] = useState(false);
  const [groups, setGroups] = useState(false);
  const [sessions, setSessions] = useState();
  const [showDetails, setShowDetails] = useState(false);
  const [showResults, setShowResults] = useState(false);
  const [details, setDetails] = useState([]);
  const [results, setResults] = useState({});
  const db = firebase.firestore();

  const getGroups = async () => {
    try {
      await db.collection("groups").get().then((querySnapshot) => {
        const parsed = [];
        querySnapshot.forEach((doc) => {
            parsed.push({
              uid: doc.id,
              ...doc.data()
            });
        });
        setGroups(parsed);
        formatSessions(parsed);
      });
    } catch {
      
    }
  }

  const email = async (obj) => {
    try {
      message.loading({ content: 'Ð—Ð°Ð³Ñ€ÑƒÐ·ÐºÐ°', key: 'send-mail' });
      await sendEmail({
        name: obj.name,
        group: obj.group,
        lastName: obj.lastName,
        html: ReactDOMServer.renderToStaticMarkup(
          <EmaiQuestions emailing={true} obj={obj} />
        )
      })
      message.success({ content: 'ÐŸÐ¸ÑÑŒÐ¼Ð¾ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¾', key: 'send-mail' });
    } catch (e) {
      console.log(e);
      message.error({ content: 'ÐŸÑ€Ð¾Ð¸Ð·Ð¾ÑˆÐ»Ð° Ð¾ÑˆÐ¸Ð±ÐºÐ° Ð¿Ñ€Ð¸ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ð¸ Ð¿Ð¸ÑÑŒÐ¼Ð°', key: 'send-mail' });
    }
  }

  const showModal = (details) => {
    setDetails(details);
    setShowDetails(true);
  }

  const showResultsModal = (res) => {
    setResults(res);
    setShowResults(true);
  }

  const formatSessions = async (groupsParsed = groups) => {
    setSessions(old => old.map(el => {
      const amount = el.groups.reduce((acc, x) => {
        return acc + groupsParsed.find((item) => item.group === x).studentAmount;
      }, 0)
      return {...el, studentsAmount: amount};
    }));
    setFetched(true);
  }

  const showDeleteModal = async (sessionId) => {
    Modal.confirm({
      title: 'Ð’Ð½Ð¸Ð¼Ð°Ð½Ð¸Ðµ',
      icon: <ExclamationCircleOutlined />,
      content: 'Ð£Ð´Ð°Ð»Ð¸Ð² ÑÐµÑÑÐ¸ÑŽ Ð²Ñ‹ Ð¾Ñ‡Ð¸ÑÑ‚Ð¸Ñ‚Ðµ Ð¼ÐµÑÑ‚Ð¾ Ð² Ð‘Ð”, Ð½Ð¾ Ð¿Ð¾Ñ‚ÐµÑ€ÑÐµÑ‚Ðµ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚ÑŒ Ð¿Ñ€Ð¾ÑÐ¼Ð¾Ñ‚Ñ€Ð° Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð¾Ð², Ð²Ñ‹ Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ñ…Ð¾Ñ‚Ð¸Ñ‚Ðµ Ð¿Ñ€Ð¾Ð´Ð¾Ð»Ð¶Ð¸Ñ‚ÑŒ?',
      okText: 'Ð”Ð°',
      cancelText: 'ÐÐµÑ‚',
      onOk() {
        deleteSession(sessionId);
      },
    });
  }

  const deleteSession = async (sessionId) => {
    try {
      message.loading({ content: 'Ð—Ð°Ð³Ñ€ÑƒÐ·ÐºÐ°', key: 'send-mail' });
      await db.collection("sessions").doc(sessionId).delete();
      message.success({ content: 'Ð£ÑÐ¿ÐµÑˆÐ½Ð¾', key: 'send-mail' });
      getSessions();
    } catch (e) {
      message.error({ content: 'ÐŸÑ€Ð¾Ð¸Ð·Ð¾ÑˆÐ»Ð° Ð¾ÑˆÐ¸Ð±ÐºÐ° Ð¿Ñ€Ð¸ ÑƒÐ´Ð°Ð»ÐµÐ½Ð¸Ð¸, Ð¿Ð¾Ð¿Ñ€Ð¾Ð±ÑƒÐ¹Ñ‚Ðµ Ð¿Ð¾Ð·Ð¶Ðµ', key: 'send-mail' });
    }
  }

  const getSessions = async () => {
    setFetched(false);
    try {
      const _result = [];
      const _sessions = await db.collection("sessions").get();
      for (const session of _sessions.docs) {
        const doc = await session.ref.collection('attempts').get();
        let attempts = [];
        let attemptsLength = 0;
        if (!doc.empty) {
          attempts = doc.docs.map(el => { 
            return { ...el.data(), studentId: el.id}
          });
          attemptsLength = attempts.length;
        }
        _result.push({...session.data(), sessionId: session.id, attemptsLength: attemptsLength, attempts: attempts})
      }
      setSessions(_result);
      getGroups();
    } catch (e) {
      message.error({ content: 'ÐÐµ ÑƒÐ´Ð°Ð»Ð¾ÑÑŒ Ð¿Ð¾Ð»ÑƒÑ‡Ð¸Ñ‚ÑŒ ÑÐ¿Ð¸ÑÐ¾Ðº ÑÐµÑÑÐ¸Ð¹', key: 'add-student' });
    }
  }

  useEffect(() => {
    getSessions();
  }, [])

  return (
    <ActiveSessions fetched={fetched} showDeleteModal={showDeleteModal} results={results} showResults={showResults} setShowResults={setShowResults} showResultsModal={showResultsModal} details={details} email={email} showDetails={showDetails} setShowDetails={setShowDetails} showModal={showModal} sessions={sessions} {...props} />
  );
};

export default ActiveSessionsContainer;