import React, { useEffect, useState } from 'react';
import { useLocation, Redirect, useHistory } from 'react-router';
import Test from './Test';
import { message, Modal } from 'antd';
import { ExclamationCircleOutlined} from '@ant-design/icons';
import firebase from 'firebase';
import 'firebase/firestore';
import { useSelector } from 'react-redux';
import moment from 'moment';
import sendEmail from '../../utils/sendEmail';
import ReactDOMServer from 'react-dom/server';
import EmaiQuestions from '../../components/EmaiQuestions/EmaiQuestions';

const addZero = value => {
  if (value < 10) return '0' + value;
  return value;
};


const TestContainer = props => {
  const [showDrawer, setShowDrawer] = useState(false);
  const [currQuestion, setCurrQuestion] = useState(0);
  const location = useLocation();
  const [test, setTest] = useState(location?.state?.test);
  const [timeLeft, setTimeLeft] = useState();
  const sessionId = location?.state?.sessionId;
  const [limit] = useState(Date.now() + location?.state?.limit);
  const [testLength] = useState(location?.state?.test?.questions?.length);
  const [showModal, setShowModal] = useState(false);
  const history = useHistory();
  const db = firebase.firestore();
  const userState = useSelector(store => store);

  const nextQuestion = () => {
    setCurrQuestion(old => old < (testLength - 1) ? old + 1 : old);
  }

  const previousQuestion = () => {
    setCurrQuestion(old => old > 0 ? old - 1: old);
  }

  const setCurrentQuestion = (value) => {
    setCurrQuestion(value);
  }

  const sendResults = async (force) => {
    try {
      message.loading({ content: 'ÐžÑ‚Ð¿Ñ€Ð°Ð²ÐºÐ° Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð¾Ð²', key: 'send-res'})
      const minutesHave = moment.duration(location?.state?.limit).hours() * 60 + moment.duration(location?.state?.limit).minutes();
      const minutesLeft = moment.duration(timeLeft).hours() * 60 + moment.duration(timeLeft).minutes();
      const score = (test.questions.map(el => el.answers.every(item => item.isRight === item.selected)).filter(el => el).length / testLength * 100).toFixed(1);
      const elapsedTime = +minutesHave - +minutesLeft + ':' + addZero(+moment.duration(location?.state?.limit).seconds() || 60 - +moment.duration(timeLeft).seconds());
      const result = {...test, score: score, elapsedTime: elapsedTime, name: userState.name, group: userState.group, lastName: userState.lastName, testName: test.name};
      await db.collection('sessions').doc(sessionId).collection('attempts').doc(userState.uid).set(result);
      await sendEmail({
        name: result.name,
        group: result.group,
        lastName: result.lastName,
        html: ReactDOMServer.renderToStaticMarkup(
          <EmaiQuestions emailing={true} obj={result} />
        )
      })
      message.success({ content: 'Ð ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ‹ ÑƒÑÐ¿ÐµÑˆÐ½Ð¾ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð»ÐµÐ½Ñ‹', key: 'send-res'});
      history.push('/tests');
    } catch (e) {
      if (force) {
        history.push('/tests');
        message.error({ content: 'Ð’Ñ€ÐµÐ¼Ñ Ð²Ñ‹ÑˆÐ»Ð¾.', key: 'send-res'})
      } else message.error({ content: 'ÐžÑˆÐ¸Ð±ÐºÐ°.', key: 'send-res'})
    }
  }

  const confirmSending = () => {
    Modal.confirm({
      title: 'Ð’Ð½Ð¸Ð¼Ð°Ð½Ð¸Ðµ',
      icon: <ExclamationCircleOutlined />,
      content: 'ÐžÑ‚Ð¿Ñ€Ð°Ð²Ð¸Ð² Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚ Ñ‚ÐµÑÑ‚Ð° Ð²Ñ‹ Ð½Ðµ ÑÐ¼Ð¾Ð¶ÐµÑ‚Ðµ Ð¸Ð·Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ ÐµÐ³Ð¾ Ð² Ð±ÑƒÐ´ÑƒÑ‰ÐµÐ¼. Ð£Ñ‡Ð¸Ñ‚ÐµÐ»ÑŒ Ð¿Ð¾Ð»ÑƒÑ‡Ð¸Ñ‚ Ð´ÐµÑ‚Ð°Ð»ÑŒÐ½ÑƒÑŽ Ð¸Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ð¸ÑŽ Ð¾ Ñ…Ð¾Ð´Ðµ Ð¿Ñ€Ð¾Ñ…Ð¾Ð¶Ð´ÐµÐ½Ð¸Ñ Ñ‚ÐµÑÑ‚Ð°. Ð’Ñ‹ Ð´ÐµÐ¹ÑÑ‚Ð²Ð¸Ñ‚ÐµÐ»ÑŒÐ½Ð¾ Ñ…Ð¾Ñ‚Ð¸Ñ‚Ðµ Ð¾Ñ‚Ð¿Ñ€Ð°Ð²Ð¸Ñ‚ÑŒ Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ñ‹ Ð¿Ñ€Ð¾Ñ…Ð¾Ð¶Ð´ÐµÐ½Ð¸Ñ?',
      okText: 'Ð”Ð°.',
      cancelText: 'ÐÐµÑ‚.',
      onOk() {
        sendResults();
      },
    });
  }

  const markAnswer = (question, answer, index) => {
    const _test = {...test};
    if (question.multiselection) {
      question.answers = question.answers.map((el, idx) => {
        if (idx === index) {
          return {...el, selected: !el.selected}
        }
        return el;
      })
    } else {
      question.answers = question.answers.map((el, idx) => {
        if (idx === index) {
          return {...el, selected: !el.selected}
        }
        return {...el, selected: false};
      })
    }
    setTest(_test);
  }

  useEffect(() => {
    const interval = setInterval(() => {
      const _timeLeft = limit - Date.now();
      if (timeLeft < _timeLeft ) {
        setTimeLeft(0);
        sendResults(true);
      }
      if (timeLeft < 0) {
        sendResults(true);
      }
      setTimeLeft(_timeLeft);
    }, 500);
    return () => {
      clearInterval(interval);
    }
  }, [])

  if (!test) { 
    return <Redirect to='/' /> 
  }

  return (
    <Test 
      sendResults={confirmSending}
      markAnswer={markAnswer}
      showDrawer={showDrawer} 
      test={test} 
      timeLeft={timeLeft}
      setCurrentQuestion={setCurrentQuestion}
      questionIndex={currQuestion}
      nextQuestion={nextQuestion} 
      previousQuestion={previousQuestion} 
      setShowDrawer={setShowDrawer}
      currentQuestion={test.questions[currQuestion]}
      {...props} 
    />
  );
};

export default TestContainer;